//.section__titles
.section__titles
  display: flex
  justify-content: space-between

.section__titles__decoration-right
  width: 125px
  height: 120px

.services__container
  margin-top: 75px
  margin-bottom: 65px

.section__overtitle
  display: flex
  align-items: center

  margin-bottom: 10px

.section__overtitle-text
  font-weight: 400
  font-size: 20px
  line-height: 1
  color: #000000

.section__overtitle-line
  margin-left: 25px

  width: 66px
  height: 1px
  border: 1px solid rgb(197 197 197)

.section__title-title
  font-weight: 800
  font-size: 40px
  line-height: 1
  color: #000000

.services__list-block
  margin-top: 85px

  width: 100%

  @media (max-width: 440px)
    margin-top: 50px

.services__list
  width: 100%

  @media (max-width: 440px)
    overflow-x: auto
    overflow-y: hidden

.services__list-item
  display: flex
  justify-content: space-between

  width: 100%

  border-bottom: 1px solid rgb(197 197 197)

  @media (max-width: 1160px)
    justify-content: space-around

  @media (max-width: 780px)
    justify-content: space-between

  @media (max-width: 440px)
    width: 500px

  &:nth-child(1)
    border-top: 1px solid rgb(197 197 197)
    border-bottom: 1px solid rgb(197 197 197)

.services__list-item-title-block
  display: flex
  align-items: flex-start

  max-width: 315px

  width: 100%

  border-right: 1px solid rgb(197 197 197)

  position: relative

  @media (max-width: 1160px)
    max-width: 210px

  @media (max-width: 925px)
    max-width: 180px

  @media (max-width: 720px)
    max-width: 140px

  @media (max-width: 565px)
    max-width: 110px

.services__list-item-title-block--without-border
  border: none

.services__list-item-title-overflow-line
  border-right: 1px solid rgb(197 197 197)

  position: absolute
  top: 0
  bottom: 0
  right: 0

.services__list-item-title
  margin-top: 30px
  margin-right: 100px

  font-weight: 700
  font-size: 40px
  line-height: 1
  color: #000000

  @media (max-width: 1160px)
    font-size: 25px

  @media (max-width: 840px)
    font-size: 20px

  @media (max-width: 620px)
    font-size: 16px

.services__list-item-description
  display: flex
  justify-content: center
  align-items: flex-start

  width: 50%

  position: relative

.services__list-item-description--border-right
  border-right: 1px solid rgb(197 197 197)

.services__list-item-description-overflow-line
  width: 1px
  height: 100%

  border-right: 1px solid rgb(197 197 197)

  position: absolute
  z-index: 1
  right: 0
  bottom: 0

.services__list-item-description-content
  display: flex
  flex-direction: column
  gap: 35px

  padding: 30px 15px

  max-width: 385px

  width: 100%

.services__list-item-description-p
  font-weight: 350
  font-size: 20px
  line-height: 1
  color: #000000

  @media (max-width: 1160px)
    font-size: 18px

  @media (max-width: 620px)
    font-size: 16px

.services__list-item-description-list
  display: flex
  flex-direction: column
  gap: 10px

.services__list-item-description-list-item
  display: flex
  align-items: center
  gap: 15px

.services__list-item-description-list-item p
  font-weight: 700
  font-size: 20px
  line-height: 1
  color: #000000

  @media (max-width: 1160px)
    font-size: 16px

  @media (max-width: 620px)
    font-size: 13px

.services__list-item-description-list-item-line
  flex-shrink: 0

  width: 27px
  height: 1px

  background-color: #000

// read more
.services__list-item-read-more
  display: flex
  align-items: center
  justify-content: center

  padding: 0 15px

  width: 30%

  @media (max-width: 1160px)
    width: 15%

  @media (max-width: 780px)
    width: 20%

.services__list-item-read-more-circle
  display: flex
  align-items: center
  justify-content: center
  flex-shrink: 0

  width: 177px
  height: 177px

  border: 1px solid #D0FF48
  border-radius: 50%

  overflow: hidden

  position: relative

  @media (max-width: 845px)
    width: 135px
    height: 135px

  @media (max-width: 620px)
    width: 105px
    height: 105px

  @media (max-width: 480px)
    width: 77px
    height: 77px

  &::before
    content: ""
    display: block

    --size: 0

    width: var(--size)
    height: var(--size)

    background: radial-gradient(circle, rgba(202,247,70,1) 100%, rgba(202,247,70,1) 100%, rgba(208,255,72,1) 100%)
    border-radius: 50%
    transition: all .6s ease-in-out

    transform: translate(-50%, -50%)

    position: absolute
    left: var(--x)
    top: var(--y)

  &:hover::before
    --size: 400px

.services__list-item-read-more-content
  display: flex
  align-items: center
  gap: 10px

  cursor: pointer

  position: relative
  pointer-events: none

  @media (max-width: 490px)
    flex-direction: column

.services__list-item-read-more-content-p
  width: min-content

  font-weight: 700
  font-size: 18px
  line-height: 1
  text-align: center
  color: #000000

  @media (max-width: 845px)
    font-size: 14px

  @media (max-width: 620px)
    font-size: 12px

.services__list-item-read-more-content-arrow
  @media (max-width: 845px)
    width: 15px
    height: 15px