.products
  background-color: #1B1B1B

  padding-top: 120px
  padding-bottom: 85px

  overflow: hidden

  position: relative

  @media (max-width: 1015px)
    padding-top: 80px

// decoration
.products__decoration-circle
  position: absolute
  right: 0
  bottom: 0

// titles
.section__titles--white
  align-items: center

  margin-bottom: 85px

  position: relative
  z-index: 1

.section__titles__decoration-right--white
  display: flex
  align-items: center

.products__decoration-right-image
  animation: 2.5s linear infinite rot

  transform-origin: center

@keyframes rot
  0%
    transform: rotate(0deg)
  100%
    transform: rotate(360deg)

.section__overtitle-text--white,
.section__title-title--white
  color: #fff!important

// gallery
.products__gallery
  display: flex
  justify-content: space-between
  flex-wrap: wrap
  gap: 50px

  width: 100%

  @media (max-width: 1530px)
    display: grid
    grid-template-columns: 1fr 1fr

  @media (max-width: 1015px)
    grid-template-columns: 1fr
    justify-items: center

.products__gallery-items-column
  &:nth-child(2)
    margin-top: 70px

  &:nth-child(3)
    margin-top: 30px

.products__gallery-items-column-item
  display: flex
  flex-direction: column
  align-items: center
  justify-content: flex-end
  gap: 10px

  width: 430px
  height: 498px

  border: 1px solid #434343
  background-color: #1B1B1B
  cursor: pointer

  position: relative
  z-index: 1

  @media (max-width: 465px)
    width: 345px

  &:nth-child(even)
    @media (max-width: 1530px)
      margin-top: 70px

    @media (max-width: 1015px)
      margin-top: unset

.products__gallery-items-column-item--two
  margin-top: 70px

  @media (max-width: 1530px)
    margin-top: unset

.products__gallery-items-column-item--three
  margin-top: 30px

  @media (max-width: 1530px)
    margin-top: unset

.products__gallery-items-column-item-bottom-image
  position: relative
  z-index: 2

.products__gallery-items-column-item-bottom-image--roll
  right: -25px

  @media (max-width: 465px)
    width: 379px
    height: 259px

    right: -10px

.products__gallery-items-column-item-top-image--494-title
  position: relative
  bottom: -30px

.products__gallery-items-column-item-bottom-image--494-car
  right: -25px
  bottom: -40px

  @media (max-width: 495px)
    width: 487px
    height: 275px
    right: 0

.products__gallery-items-column-item--flagman
  justify-content: center

.products__gallery-items-column-item-top-image--flagman
  @media (max-width: 465px)
    width: 300px

.products__gallery-items-column-item-bottom-image--flagman
  @media (max-width: 465px)
    width: 300px

.products__gallery-items-column-item-top-image--hookah
  position: relative
  top: calc(-50% + 15px)
  left: 5px
  z-index: 3

.products__gallery-items-column-item-left--hookah,
.products__gallery-items-column-item-right--hookah
  width: 50%
  height: 100%
  
  position: absolute
  
.products__gallery-items-column-item-left--hookah
  background-color: #000
  
  left: 0
  top: 0
  
.products__gallery-items-column-item-right--hookah
  background-color: #fff

  right: 0
  top: 0

.products__gallery-items-column-item--advance
  justify-content: center

.products__gallery-items-column-item-bottom-image--advance
  position: absolute
  right: -45%
  top: 10%

  @media (max-width: 465px)
    width: 305px
    height: 609px
    right: -90px
    bottom: -198px
    top: unset

.products__gallery-items-column-item-bottom-image--logistic
  position: relative
  right: -50px

  @media (max-width: 465px)
    width: 356px
    height: 260px

    right: -20px

.products__gallery-items-column-item-top-image--logistic
  @media (max-width: 465px)
    margin-right: -55px